@charset "UTF-8";
@import url("https://netdna.bootstrapcdn.com/font-awesome/4.0.3/css/font-awesome.css");
@import url(https://fonts.googleapis.com/css?family=Oswald:700);
@import url(https://fonts.googleapis.com/css?family=Droid+Serif);
.pcs,
.cur,
.per {
  white-space: nowrap;
}

.pcs:after {
  content: " pcs";
}

.cur:before {
  content: "$";
}

.per:after {
  content: "%";
}

table {
  position: relative;
  width: 100%;
}
table th {
  text-align: left;
  vertical-align: bottom;
  /* border-bottom: 1px solid #373737; */
  text-transform: capitalize;
  font: 0.8em "Oswald", sans-serif;

  /* border-right: 1px solid #373737; */
}

table th,
table td {
  padding: 0.4em;
  border: 1px solid #373737;
}
.border_none {
  border: none;
  border-left: 1px solid #373737;
}
table.fold-table {
  background: #0d0d0d;
  color: #fff;
}
table.fold-table > tbody > tr.view {
  /* font-family: "Droid Serif", serif; */
  transition: all 0.3s ease;
  text-align: center;
}
table.fold-table > tbody > tr.view td,
table.fold-table > tbody > tr.view th {
  cursor: pointer;
}
table.fold-table > tbody > tr.view td:first-child,
table.fold-table > tbody > tr.view th:first-child {
  position: relative;
  /* padding-left: 20px; */
}
table.fold-table > tbody > tr.view td:first-child:before,
table.fold-table > tbody > tr.view th:first-child:before {
  position: absolute;
  top: 50%;
  right: 15px;
  width: 9px;
  height: 16px;
  margin-top: -8px;
  font: 16px fontawesome;
  content: "ïƒ—";
  transition: all 0.3s ease;
}
/* table.fold-table > tbody > tr.view:nth-child(4n-1) {
  background: black;
} */
table.fold-table > tbody > tr.view:hover {
  background: #1c1c1c;
}
table.fold-table > tbody > tr.view.open {
  background: #1c1c1c;
  color: white;
}
table.fold-table > tbody > tr.view.open td:first-child:before,
table.fold-table > tbody > tr.view.open th:first-child:before {
  transform: rotate(-180deg);
  color: white;
}
table.fold-table > tbody > tr.fold {
  display: none;
}
table.fold-table > tbody > tr.fold.open {
  display: table-row;
}
.fold-content th,
tbody {
  text-align: center;
}
.fold-content {
  padding: 0.5em;
}
.fold-content h3 {
  margin-top: 0;
}
.fold-content > table {
  background: #1c1c1c;
  color: white;
  box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.2);
}
.fold-content > table > tbody > tr {
  font-family: "Droid Serif", serif;
  cursor: pointer;
}
/* .fold-content > table > tbody > tr:nth-child(even) {
  background: #eee;
} */
.fold-content > table > tbody > tr:hover {
  background: #7749b4;
}

.visible-small {
  display: none;
}

.visible-big {
  display: block;
}
table th {
  padding-top: 15px;
  padding-bottom: 15px;
}
tfoot th {
  text-align: center;
}
@media (max-width: 700px) {
  /* body {
    font-size: 0.8em;
    padding: 0 10px;
  } */

  table th,
  table td {
    padding: 1em 0.2em;
  }
  table th {
    vertical-align: bottom;
    padding-bottom: 0;
  }

  table.small-friendly > thead {
    display: none;
  }
  table.small-friendly > tbody > tr > td,
  table.small-friendly > tbody > tr > th {
    position: relative;
    padding: 0;
    padding-left: 50%;
    /* display: block; */
    vertical-align: top;
    /* border-bottom: 1px solid rgba(0, 0, 0, 0.05); */
  }
  table.small-friendly > tbody > tr > td:before,
  table.small-friendly > tbody > tr > th:before {
    position: absolute;
    top: 0;
    left: 0.5em;
    max-width: 50%;
    content: attr(data-th) ": ";
    display: block;
    font-weight: bold;
  }
  table.small-friendly > tbody > tr > td:first-child,
  table.small-friendly > tbody > tr > th:first-child {
    margin-top: 1em;
  }
  table.small-friendly > tbody > tr > td:last-child,
  table.small-friendly > tbody > tr > th:last-child {
    margin-bottom: 1em;
    border: 0;
  }

  .visible-small {
    display: block;
  }

  .visible-big {
    display: none;
  }
}
